import React, { Suspense, lazy } from 'react';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';
import Layout from './components/Layout';
import LoadingSpinner from './components/LoadingSpinner';

// Lazy load components
const Dashboard = lazy(() => import('./pages/Dashboard'));
const StudentList = lazy(() => import('./pages/students/StudentList'));
const StudentProfile = lazy(() => import('./pages/students/StudentProfile'));
const StudentForm = lazy(() => import('./pages/students/StudentForm'));
const ClassSchedule = lazy(() => import('./pages/students/ClassSchedule'));
const Gradebook = lazy(() => import('./pages/students/Gradebook'));
const GradeViewer = lazy(() => import('./pages/students/GradeViewer'));
const Attendance = lazy(() => import('./pages/students/Attendance'));
const ParentPortal = lazy(() => import('./pages/students/ParentPortal'));
const FeeStructure = lazy(() => import('./pages/fees/FeeStructure'));
const FeeCollection = lazy(() => import('./pages/fees/FeeCollection'));
const Reports = lazy(() => import('./pages/reports/Reports'));

// Loading component
const LoadingFallback = () => (
  <div className="min-h-screen flex items-center justify-center">
    <LoadingSpinner size="large" color="blue" />
  </div>
);

function App() {
  return (
    <Router>
      <Layout>
        <Suspense fallback={<LoadingFallback />}>
          <Routes>
            <Route path="/" element={<Dashboard />} />
            <Route path="/students" element={<StudentList />} />
            <Route path="/students/new" element={<StudentForm />} />
            <Route path="/students/:id/edit" element={<StudentForm />} />
            <Route path="/students/:id" element={<StudentProfile />} />
            <Route path="/students/schedule" element={<ClassSchedule />} />
            <Route path="/students/grades" element={<Gradebook />} />
            <Route path="/students/grades/view" element={<GradeViewer />} />
            <Route path="/students/attendance" element={<Attendance />} />
            <Route path="/parent-portal" element={<ParentPortal />} />
            <Route path="/fees/structure" element={<FeeStructure />} />
            <Route path="/fees/collection" element={<FeeCollection />} />
            <Route path="/reports" element={<Reports />} />
          </Routes>
        </Suspense>
      </Layout>
    </Router>
  );
}

export default App;
